.container {
  position: relative;
  width: 1330px;
  margin: 0 auto;
  padding: 0 20px; }

@media (max-width: 1329px) {
  .container {
    width: 1000px; } }

@media (max-width: 999px) {
  .container {
    width: 700px; } }

@media (max-width: 699px) {
  .container {
    width: 540px; } }

@media (max-width: 580px) {
  .container {
    width: 100%; } }

:root {
  --white: #ffffff;
  --black: #000000;
  --black-a-10: rgba(0, 0, 0, 0.1);
  --black-a-5: rgba(0, 0, 0, 0.05);
  --primary: #34a853;
  --primary-light: #e1f0e5;
  --gray-100: #f4f5f9;
  --gray-900: #22252f;
  --Roboto: 'Roboto', 'Helvetica', 'Arial', sans-serif; }

html {
  color: var(--gray-900);
  font-family: var(--Roboto);
  font-size: 14px;
  line-height: 1.14;
  scroll-behavior: smooth; }

body {
  margin: 0;
  background-color: var(--gray-100); }

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none; }

body {
  position: relative; }

*,
*::before,
*::after {
  box-sizing: border-box;
  font-variant-numeric: lining-nums; }

fieldset {
  border: none; }

input::-moz-placeholder {
  opacity: 1; }

input::placeholder {
  opacity: 1; }

input[type='number'] {
  -moz-appearance: textfield; }
  input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

select::-ms-expand {
  display: none; }

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0; }

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 48px;
  padding: 0 41px;
  background-color: var(--white);
  border: none;
  border-radius: 24px;
  color: var(--primary);
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.2s; }
  .button__icon {
    flex-shrink: 0;
    width: 25px;
    height: 25px;
    margin-right: 15px; }
  .button_color_primary {
    background-color: var(--primary);
    color: var(--white); }
  .button_shadow {
    box-shadow: 0px 4px 10px var(--black-a-10); }
    .button_shadow:active {
      box-shadow: none; }

.cite {
  font-size: 18px;
  font-weight: 900;
  font-style: normal; }
  .cite_color_white {
    color: var(--white); }

.footer {
  padding-top: 37px;
  padding-bottom: 50px;
  border-top: 1px solid var(--black-a-5); }
  .footer__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    line-height: 1.7; }

.header {
  position: sticky;
  top: 0;
  background-color: var(--gray-100);
  z-index: 20; }
  .header__inner {
    display: grid;
    grid-template-columns: -webkit-max-content 1fr -webkit-min-content;
    grid-template-columns: max-content 1fr min-content;
    align-items: center;
    -moz-column-gap: 10px;
         column-gap: 10px;
    padding-top: 10px;
    padding-bottom: 16px; }
  .header .main-menu {
    justify-self: end; }

@media (max-width: 699px) {
  .header__inner {
    grid-template-columns: 1fr -webkit-min-content;
    grid-template-columns: 1fr min-content;
    justify-content: space-between;
    row-gap: 10px; }
  .header .main-menu {
    grid-column: 1 / -1;
    grid-row: 2 / 3;
    justify-self: start; } }

.heading {
  display: block;
  margin-top: 0;
  font-weight: bold; }
  .heading_level_1 {
    margin-bottom: 22px;
    font-size: 36px;
    line-height: 1.14; }
  .heading_color_white {
    color: var(--white); }

@media (max-width: 999px) {
  .heading_level_1 {
    font-size: 28px; } }

@media (max-width: 540px) {
  .heading_level_1 {
    font-size: 20px; } }

.hero-swiper__inner {
  height: 505px; }

.hero-swiper div.swiper-pagination {
  bottom: 44px; }

.hero-swiper span.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  background: var(--white);
  opacity: 0.5; }

.hero-swiper span.swiper-pagination-bullet-active {
  opacity: 1; }

.hero-swiper-slide {
  display: flex;
  height: 100%;
  padding-bottom: 100px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover; }
  .hero-swiper-slide__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%; }

.icon-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  background-color: var(--primary);
  border-radius: 10px; }
  .icon-button__icon {
    width: 25px;
    height: 25px;
    color: var(--white); }
  .icon-button:hover, .icon-button:focus {
    background-color: var(--hover); }
  .icon-button:active {
    background-color: var(--active); }

.icon-link {
  display: inline-flex;
  align-items: center;
  color: var(--black);
  font-size: 14px;
  text-decoration: none; }
  .icon-link__icon {
    flex-shrink: 0;
    width: 18px;
    height: 18px;
    margin-right: 6px;
    color: var(--primary); }
  .icon-link:hover, .icon-link:focus {
    text-decoration: underline; }
  .icon-link_color_white {
    color: var(--white); }
  .icon-link_color_total-white {
    color: var(--white); }
    .icon-link_color_total-white .icon-link__icon {
      color: var(--white); }
  .icon-link_size_big {
    font-size: 16px;
    line-height: 1.5; }
    .icon-link_size_big .icon-link__icon {
      width: 80px;
      height: 80px;
      margin-right: 28px; }

.icons-list {
  display: flex;
  justify-content: space-between; }
  .icons-list__item:first-child {
    border-right: 1px solid var(--gray-500); }

@media (max-width: 999px) {
  .icons-list {
    flex-wrap: wrap;
    justify-content: center; }
    .icons-list__item {
      flex-basis: 33.3333%; }
      .icons-list__item:first-child {
        border-right: none; } }

@media (max-width: 540px) {
  .icons-list__item {
    flex-basis: 50%; } }

@media (max-width: 400px) {
  .icons-list__item {
    flex-basis: 100%; } }

.link {
  padding: 0;
  font-size: 14px;
  background-color: transparent;
  border: none;
  color: var(--gray-900);
  font-weight: 500;
  text-decoration: none; }
  .link:hover, .link:focus {
    text-decoration: underline; }
  .link_active {
    color: var(--primary); }

.list {
  font-size: 16px; }
  .list_color_white {
    color: var(--white); }

.list-item__icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 13px;
  color: var(--primary);
  vertical-align: middle; }

.list-item:nth-last-child(n + 2) {
  margin-bottom: 10px; }

.list-item_simple::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 15px;
  background: radial-gradient(circle at 30%, var(--primary) 30%, transparent 30%);
  color: var(--primary);
  vertical-align: middle; }

.logo {
  display: inline-flex;
  align-items: center;
  color: var(--gray-900);
  font-weight: 500;
  text-decoration: none; }

@media (max-width: 540px) {
  .logo__tagline {
    display: none; } }

.main-menu {
  display: flex;
  flex-wrap: wrap;
  row-gap: 10px; }
  .main-menu__item {
    flex-shrink: 0; }
    .main-menu__item:nth-last-child(n + 2) {
      margin-right: 15px; }

.text-block {
  margin-top: 0;
  line-height: 1.8; }
  .text-block_color_white {
    color: var(--white); }
  .text-block_align_center {
    text-align: center; }
  .text-block_size_big {
    font-size: 20px;
    line-height: 1.4; }
  .text-block_size_extra-big {
    font-size: 28px;
    font-weight: bold;
    line-height: 1.18; }

@media (max-width: 540px) {
  .text-block_size_extra-big {
    font-size: 18px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  bottom: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.visibility_hidden {
  visibility: hidden;
  pointer-events: none; }

.overflow_hidden {
  overflow: hidden; }

.d_flex {
  display: flex; }

.d_none {
  display: none; }
